    // 格式化时间
    formatTime(timeString) {
      if (!timeString) return '未知';
      
      try {
        const date = new Date(timeString);
        return `${date.getHours().toString().padStart(2, '0')}:${date.getMinutes().toString().padStart(2, '0')}`;
      } catch (e) {
        return timeString;
      }
    },
    
    calculateBiorhythm() {
      if (!this.birthdate) {
        alert('请输入出生日期');
        return;
      }

      // 调用后端API计算生物节律
      this.callBiorhythmAPI();
    },

    // 调用后端生物节律API
    callBiorhythmAPI() {
      const requestData = {
        birthdate: this.birthdate,
        targetDate: this.targetDate,
        coordinates: this.userCoordinates,
        enableMoonPhaseInfluence: this.enableMoonPhaseInfluence,
        enableZodiacInfluence: this.enableZodiacInfluence
      };

      axios.post('/api/biorhythm/calculate', requestData, {
        timeout: 15000,
        headers: {
          'Content-Type': 'application/json',
          'X-Requested-With': 'XMLHttpRequest'
        }
      })
      .then(response => {
        if (response.data && response.data.success) {
          this.processBiorhythmResponse(response.data);
        } else {
          alert('计算失败: ' + (response.data.message || '未知错误'));
        }
      })
      .catch(error => {
        console.error('生物节律计算失败:', error);
        if (error.code === 'ECONNABORTED') {
          alert('请求超时，请检查网络连接');
        } else if (error.response) {
          alert(`服务器错误: ${error.response.status}`);
        } else {
          alert('网络连接失败，请稍后重试');
        }
      });
    },

    // 处理后端返回的生物节律数据
    processBiorhythmResponse(data) {
      // 设置基础信息
      this.age = data.age;
      this.daysSinceBirth = data.daysSinceBirth;
      
      // 设置生物节律值
      this.biorhythm.physical = data.biorhythm.physical;
      this.biorhythm.emotional = data.biorhythm.emotional;
      this.biorhythm.intellectual = data.biorhythm.intellectual;
      
      // 设置生肖信息
      if (data.zodiacInfo) {
        this.chineseZodiac = data.zodiacInfo.zodiac;
        this.setZodiacImage(data.zodiacInfo.zodiac);
        // 存储完整的生肖信息，供页面显示使用
        this.zodiacInfo = data.zodiacInfo;
      }
      
      // 设置月相信息
      if (data.moonPhaseInfo) {
        this.moonPhaseInfo = {
          success: data.moonPhaseInfo.success,
          location: data.moonPhaseInfo.location,
          moonPhase: data.moonPhaseInfo.moonPhase,
          illumination: data.moonPhaseInfo.illumination,
          moonrise: data.moonPhaseInfo.moonrise,
          moonset: data.moonPhaseInfo.moonset,
          influenceDescription: data.moonPhaseInfo.influenceDescription
        };
      }
      
      this.showResults = true;
      
      // 在下一个DOM更新周期后绘制图表
      this.$nextTick(() => {
        this.drawChart();
      });
    },

    // 设置生肖图片
    setZodiacImage(zodiac) {
      const zodiacImageNameMap = {
        '鼠': 'Rat',
        '牛': 'Ox',
        '虎': 'Tiger',
        '兔': 'Rabbit',
        '龙': 'Dragon',
        '蛇': 'Snake',
        '马': 'Horse',
        '羊': 'Goat',
        '猴': 'Monkey',
        '鸡': 'Rooster',
        '狗': 'Dog',
        '猪': 'Pig'
      };

      const imageName = zodiacImageNameMap[zodiac];
      if (imageName) {
        try {
          this.chineseZodiacImage = require(`@/assets/img/zodiac/${imageName}.jpeg`);
        } catch (e) {
          console.warn(`生肖图片 src/assets/img/zodiac/${imageName}.jpeg 未找到: ${e.message}`);
          this.chineseZodiacImage = '';
        }
      } else {
        console.warn(`未找到生肖 '${zodiac}' 对应的图片文件名映射。`);
        this.chineseZodiacImage = '';
      }
    },
    

    
    formatDate(dateString) {
      const date = new Date(dateString);
      return `${date.getFullYear()}年${date.getMonth() + 1}月${date.getDate()}日`;
    },
    
    getProgressWidth(value) {
      // 将-1到1的值转换为0到100%的宽度
      return `${((value + 1) / 2 * 100).toFixed(0)}%`;
    },
    
    getProgressColor(value) {
      // 根据值的不同返回不同的颜色
      if (value > 0.5) return '#4CAF50'; // 高值 - 绿色
      if (value > 0) return '#8BC34A'; // 中高值 - 浅绿色
      if (value > -0.5) return '#FFC107'; // 中低值 - 黄色
      return '#F44336'; // 低值 - 红色
    },
    
    getPhysicalDescription() {
      const value = this.biorhythm.physical;
      
      // 判断是否为临界日
      if (this.isCriticalDay(value)) {
        return '今天是体能节律的临界日，状态不稳定，建议避免重要的体力活动和决策。';
      }
      
      if (value > 0.7) return '您的体能状态极佳，适合进行体力活动和运动训练。';
      if (value > 0.3) return '您的体能状态良好，精力充沛，适合完成需要体力的工作。';
      if (value > 0) return '您的体能状态处于高潮期，体能较为充沛。';
      if (value > -0.3) return '您的体能状态处于低潮期，注意适当休息，不要过度劳累。';
      if (value > -0.7) return '您的体能状态较差，建议减少体力消耗，多休息。';
      return '您的体能状态处于低谷期，应避免剧烈运动，注意体能保养。';
    },
    
    getEmotionalDescription() {
      const value = this.biorhythm.emotional;
      
      // 判断是否为临界日
      if (this.isCriticalDay(value)) {
        return '今天是情感节律的临界日，情绪可能不稳定，建议避免重要的情感决策。';
      }
      
      if (value > 0.7) return '您的情绪状态极佳，充满积极性和创造力，适合社交活动。';
      if (value > 0.3) return '您的情绪状态良好，心情愉悦，适合处理人际关系。';
      if (value > 0) return '您的情绪状态处于高潮期，情绪较为积极。';
      if (value > -0.3) return '您的情绪状态处于低潮期，可能有些波动，注意情绪管理。';
      if (value > -0.7) return '您的情绪状态较差，可能感到焦虑或不安，建议放松心情。';
      return '您的情绪状态处于低谷期，应避免重要的情感决策，多进行自我关怀。';
    },
    
    getIntellectualDescription() {
      const value = this.biorhythm.intellectual;
      
      // 判断是否为临界日
      if (this.isCriticalDay(value)) {
        return '今天是智力节律的临界日，思维可能不稳定，建议避免重要的决策和复杂思考。';
